import React, { useEffect, useState } from 'react';
import * as THREE from 'three';

const ThreeComponent = () => {
  const camera: THREE.PerspectiveCamera = new THREE.PerspectiveCamera(70, 300 / 300, 0.01, 10);
  const scene: THREE.Scene = new THREE.Scene();
  const geometry: THREE.Geometry | THREE.BufferGeometry = new THREE.BoxGeometry(0.3, 0.3, 0.3);
  const material: THREE.Material | THREE.Material[] = new THREE.MeshNormalMaterial();
  const mesh: THREE.Mesh = new THREE.Mesh(geometry, material);
  const [renderer, setRenderer] = useState(null);

  camera.position.z = 1;
  scene.add(mesh);

  useEffect(() => {
    setRenderer(new THREE.WebGLRenderer({
      antialias: true,
      alpha: true
    }));

    window.onresize = onWindowResized.bind(this);

    return () => {};
  }, []);

  const onWindowResized = () => {
    const { innerWidth: width, innerHeight: height } = window;
    
    
  }

  useEffect(() => {
    if (renderer) animate();
  }, [renderer]);

  const animate = () => {
    requestAnimationFrame(animate);
    mesh.rotation.x += 0.03;
    mesh.rotation.y += 0.03;
    renderer.render(scene, camera);
  };

  const appendCanvas = (ref) => {
    if (ref && renderer) {
      const width = ref.clientWidth || 300;
      const height = ref.clientHeight || 300;

      renderer.setSize(width, height);
      camera.setViewOffset(width, height, 0, 0, width, height);
      ref.appendChild(renderer.domElement);
    }
  }

  return (<div className="canvas__wrapper" ref={appendCanvas} />);
};

export default ThreeComponent;
